.hero {
    position: relative;
    width: 100%;
    height: 90vh;
    /* Tall hero */
    display: flex;
    align-items: center;
    overflow: hidden;
}

.heroBackground {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

/* Background Image styling */
.heroImage {
    width: 100%;
    height: 100%;
    background-image: url('https://images.unsplash.com/photo-1536440136628-849c177e76a1?q=80&w=2525&auto=format&fit=crop');
    background-size: cover;
    background-position: center top;
    opacity: 0.7;
    /* User wanted "opaco", letting some gradient through */
}

/* Gradients to blend into the theme */
.gradientOverlay {
    position: absolute;
    inset: 0;
    background: radial-gradient(circle at 70% 30%, transparent 0%, rgba(15, 32, 39, 0.4) 50%, rgba(15, 32, 39, 1) 100%);
    z-index: 1;
}

.leftVignette {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 60%;
    background: linear-gradient(to right, #0F2027 0%, transparent 100%);
    z-index: 2;
}

.bottomVignette {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 300px;
    background: linear-gradient(to top, #0F2027 0%, transparent 100%);
    z-index: 2;
}

/* Content */
.content {
    position: relative;
    z-index: 10;
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 3rem;
    margin-top: 5rem;
    /* Offset for navbar */
}

.originalLabel {
    font-size: 0.9rem;
    font-weight: 700;
    text-transform: uppercase;
    color: #aaa;
    letter-spacing: 2px;
    margin-bottom: 0.5rem;
}

.title {
    font-size: 5rem;
    font-weight: 800;
    line-height: 1.1;
    margin-bottom: 1.5rem;
    color: #fff;
    text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
}

.meta {
    display: flex;
    align-items: center;
    gap: 1rem;
    font-size: 0.95rem;
    color: #ddd;
    margin-bottom: 2rem;
    font-weight: 500;
}

.newBadge {
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.3);
    padding: 2px 6px;
    border-radius: 4px;
    font-size: 0.8rem;
    font-weight: 600;
    color: #fff;
}

.rating {
    color: #FFD700;
    /* Gold */
}

.buttons {
    display: flex;
    align-items: center;
    gap: 2rem;
    margin-bottom: 2rem;
}

.watchBtn {
    display: flex;
    align-items: center;
    gap: 0.8rem;
    background-color: #fff;
    color: #000;
    padding: 0.8rem 2.5rem;
    border-radius: 50px;
    font-size: 1.1rem;
    font-weight: 700;
    transition: transform 0.2s, background-color 0.2s;
}

.watchBtn:hover {
    transform: scale(1.05);
    background-color: #f0f0f0;
}

.actionBtn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.3rem;
    color: #ccc;
    font-size: 0.75rem;
    transition: color 0.2s;
}

.actionBtn:hover {
    color: #fff;
}

.actionBtn svg {
    transition: transform 0.2s;
}

.actionBtn:hover svg {
    transform: scale(1.1);
}

.description {
    max-width: 600px;
    color: #ccc;
    font-size: 1.1rem;
    line-height: 1.6;
    margin-bottom: 1rem;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.8);
}

.genres {
    font-size: 0.9rem;
    color: #888;
    font-weight: 500;
}